@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&family=Poppins:wght@300;500;700;800&display=swap);
/* fonts */


:root{
  --main-color:#ea1e63;
  --l1:#5eb93d;
  --l2:#1ca7ec;
  --l3:#e64c2a;
  --l4:#f045ae;
  --l5:#d3242b;


  --d1: #f1cb0e;
  --d2: #d44e4b;
  --d3: #ea6f75;
  --d4: #05b3b6;
  --d5: #19a2f7;
  --bg1: #f5f5f5;
  --bg2:#fff;
  --text-color1:#444;
  --text-color2: #666;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all .3s;
}


#root{
  background-color: #f5f5f5;
  background-color: var(--bg1);
  height: 100%;
  padding-top: 90px;
}


#root.theme{
  --bg1: #222;
  --bg2:#333;
  --text-color1:#fff;
  --text-color2: #f5f5f5;
  
}


::-webkit-scrollbar{
  width:10px;
  padding: 0px 2px;
}

::-webkit-scrollbar-track{
  border-radius: 50px;
  background: #f5f5f5;
  background: var(--bg1);
}

::-webkit-scrollbar-thumb{
  background: #ea1e63;
  background: var(--main-color);
  border-radius: 50px;
}

.curveText{
  font-family: 'Great Vibes', cursive;
  font-size: 50px;
}


ul{
  list-style: none;
  margin-bottom: 0px;
}

h1,h2,h3,h4,h5,h6{
  text-transform: capitalize;
}

a:hover,
a:focus{
  text-decoration: none !important;
}

.section-title{
  text-align: center;
  margin: 70px 0px;
}

.section-title h2{
  color: #ea1e63;
  color: var(--main-color);
  font-weight: 700;
}

.section-title .line{
  display: block;
  width: 80px;
  height: 3px;
  background: #ea1e63;
  background: var(--main-color);
  margin: 15px auto;
  -webkit-animation: move infinite 2s;
          animation: move infinite 2s;
}


@-webkit-keyframes move{
  0%,100%{
      -webkit-transform:translateX(15px);
              transform:translateX(15px)
  }

    50%{
      -webkit-transform:translateX(-15px);
              transform:translateX(-15px)
    }
}


@keyframes move{
  0%,100%{
      -webkit-transform:translateX(15px);
              transform:translateX(15px)
  }

    50%{
      -webkit-transform:translateX(-15px);
              transform:translateX(-15px)
    }
}

::selection{
  color:#fff;
  background-color: #ea1e63;
  background-color: var(--main-color);
}

/* =====================================
              Navbar 
=======================================*/
.nav-content,
.nav-links ul,
.nav-icons ul,
.nav-icons ul a span,
.colo-menu ul,
.side-menu .cog-box,
.nav-icons ul li,
.nav-icons ul li span,
.scrollbtn

{
    display: flex;
    justify-content: center;
    align-items: center;
}

nav,
.nav-content
{
  width: 100%;
  height: 90px;
}

nav{
  background: #fff;
  background: var(--bg2);
  box-shadow: 0px 1px 5px rgba(0,0,0,.3);
}

.nav-content{
  justify-content: space-between;
}

.nav-logo .logo{
  width: 100px;
}

.nav-links, .nav-icons,
.nav-links ul,
.nav-icons ul
{
  height: 90px;
}

.nav-links ul li
{
  color: #666;
  color: var(--text-color2);
  margin-right: 20px;
  position: relative;
}

.nav-links ul li a,
.nav-icons ul li,
.nav-icons ul li i
{

  color: #444;

  color: var(--text-color1);
}

.nav-links ul li a{

    font-weight: 500;
    font-size: 21px;
}


.nav-icons ul li{
  margin-left: 30px;
  width: 35px;
  height: 35px;
  background: #f5f5f5;
  background: var(--bg1);
  border-radius: 50%;
  cursor: pointer;
  position: relative;
}

.nav-icons ul li span{
    width: 25px;
    height: 25px;
    background: #fff;
    box-shadow: 1px 1px 4px rgba(0,0,0,.2);
    border-radius: 50%;  
    position: absolute;
    top: -10px;
    left: 16px;
    font-weight: 600;
    color: #ea1e63;
    color: var(--main-color);
}


.nav-icons ul li i{
  font-size: 20px;
}


nav li.barsBtn{
  display: none;
}

/* ==============================================
                  Side menu
================================================*/

.side-menu.open{
  left:0;
}

.side-menu{
  width: 260px;
  position: fixed;
  background: #f5f5f5;
  background: var(--bg1);
  box-shadow: 0px 1px 4px rgba(0,0,0,.3);
  color: #444;
  color: var(--text-color1);
  padding: 20px;
  left: -260px;
  transition: all .4s;
  height: 100%;
  top: 0;
  bottom: 0px;
  z-index: 10;
}

.side-menu-logo{
  width: 100%;
  margin:80px auto 30px;
  z-index: 2;
  position: relative;
}

.side-menu-logo img{
  width: 100%;
  height: 100%;
}

/* =====   ==   ===========
          color menu 
===  ==========   ===== */

.side-menu .cog-box{
     position: relative;
    background:#ea1e63;
    background:var(--main-color);
    left: 240px;
    top: 150px;
    width: 32px;
    height: 32px;
    cursor: pointer;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  .side-menu .cog-box i{
    color: #fff;
    font-size: 23px;
  }
  
  .colo-menu{
    padding: 20px 20px 55px;
    position: relative;
    height: 550px;
    overflow-y: auto;
  }


.colo-menu ul{
  flex-wrap: wrap;
  justify-content: flex-start;
}

.colo-menu ul li{
  width: 52px;
  height: 35px;
  cursor: pointer;
  margin: 2px;
}

.dark-menu{
  text-align: left;
  padding:0px
}

/* ===============================
            loading screen 
=====================================*/

.loading{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    background: orangered;
    z-index:9999999
}

.loading img{
  max-width: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  display: block;
}

/* ========================================
              scroll top btn 
=========================================== */
.scrollbtn{
  width: 27px;
  height: 40px;
  background-color: #ea1e63;
  background-color: var(--main-color);
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  position: fixed;
  right: 40px;
  bottom: 70px;
  z-index: 5;
  display: none;
}

.scrollbtn.active{
  display: flex;
}



